export default {
	data() {
		return {
			username: null,
			password: null
		}
	},
	methods: {
		// 输入框用户名
		onUserNameInput(event){
			this.username = event.target.value
		},
		// 输入框密码
		onPasswordInput(event){
			console.log(event.target.value)
			this.password = event.target.value
		},
		// 如果信息不为空则请求后端
		onLogin(e){
			var that = this
			app.globalData.userInfo = e.userInfo
