			if(that.username && that.password){
				// 请求后端
				console.log('request')
			}else{
				console.log('unAll')
				uni.showModal({
					title: '信息不全',
					content: '请填写完整信息',
					showCancel: false,
					confirmText: '确定'
				});
			}
		}
	}
}
